import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import {SocketService} from './service/socket.service'
import {DataService} from './service/data.service';
import { SummaryComponent } from './summary/summary.component';
import { ModuleConnectionComponent } from './module-connection/module-connection.component';

import {AppRoutingModule} from './app-routing/app-routing.module';
import { ConfigComponent } from './config/config.component'
import { UiSwitchModule } from '../../node_modules/angular2-ui-switch/src';
import { TimerComponent } from './config/timer/timer.component';
import { SensorComponent } from './config/sensor/sensor.component';
import {ConnectionGuard} from './disconnect/connection-guard'
import {DisconnectComponent} from './disconnect/disconnect.component'


@NgModule({
  declarations: [
    AppComponent,
    SummaryComponent,
    ModuleConnectionComponent,
    ConfigComponent,
    TimerComponent,
    SensorComponent,
    DisconnectComponent
    
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    AppRoutingModule,
    UiSwitchModule
  ],
  providers: [SocketService , DataService,ConnectionGuard],
  bootstrap: [AppComponent]
})
export class AppModule { }



