import { Injectable } from '@angular/core';
import {SocketService} from './socket.service'
@Injectable()
export class DataService {

  public temp:number;
  public humi:number;
  public wt:number;
  public co2:number;
  public ec:number;
  public ph:number;

  public cm01:boolean;
  public cm02:boolean;

   public fan_timer = []
   public pump_timer = []
   public led_timer = []

   public ec_detecting = {
     'detecting': 10,
     'working': 10,
     'setpoint': 1.0,
     'enable': false
   }

   public ph_detecting = {
     'detecting': 10,
     'working': 10,
     'setpoint': 7.0,
     'enable': false
   }

   public co2_detecting = {
     'detecting': 10,
     'working': 10,
     'setpoint': 1000,
     'enable': false
   }
   

  constructor(private socket:SocketService) { 
      

  	this.socket.socket.on('SENSOR_DATA' , (data:any)=>{
  		this.temp = data['temp']
  		this.humi = data['humi']
  		// this.wt = data['wt']
  		this.co2 = data['co2']  
  		this.ph = data['ph']
  		this.ec = data['ec']
  	})

  


   
    this.socket.socket.on('REP_SETTING',(data:any)=>{
      console.log(data)
      var timer_list = data.timer_list
      var detecting_list = data.detecting_list

      // extract timer 0f fan 
      for(var t of timer_list.fan){
        var timer = {
          'start': this.extractTimer(t.start),
          'end': this.extractTimer(t.end)
        }
        this.fan_timer.push(timer)
      }

      //extract pump
      for(var t of timer_list.pump){
        var timer = {
          'start': this.extractTimer(t.start),
          'end': this.extractTimer(t.end)
        }
        this.pump_timer.push(timer)
      }
      

      for(var t of timer_list.led){
        var timer = {
          'start': this.extractTimer(t.start),
          'end': this.extractTimer(t.end)
        }
        this.led_timer.push(timer)
      }

      
      var ec = detecting_list.ec;
      this.ec_detecting.setpoint = ec.setpoint;
      this.ec_detecting.detecting = ec.detecting;
      this.ec_detecting.working = ec.working;
      this.ec_detecting.enable = ec.enable;
      
      var ph = detecting_list.ph;
      this.ph_detecting.setpoint = ph.setpoint;
      this.ph_detecting.detecting = ph.detecting;
      this.ph_detecting.working = ph.working;
      this.ph_detecting.enable = ph.enable;

      var co2 = detecting_list.co2;
      this.co2_detecting.setpoint = co2.setpoint;
      this.co2_detecting.detecting = co2.detecting;
      this.co2_detecting.working = co2.working;
      this.co2_detecting.enable = co2.enable;

    })

    this.socket.socket.emit('REQ_SETTING')


    setInterval(this.requestLoop, 1000,this.socket)
  }

  requestLoop(socket):void {
    socket.socket.emit("REQ_SENSORS", {"data": "REQ_SENSORS"})
  }

  
  extractTimer(val):any{
    //230 == 02:30
    var hour = Math.floor(val/100);
    var min = val%100;
    
    var s_hour = String(hour);
    var s_min = String(min);

    if(hour < 10){
      s_hour = "0" + s_hour;
    }
    if(min < 10){
      s_min = "0"+s_min;
    }

    var data = {
      'hour':s_hour,
      'min': s_min
    }
    return data
  }

  

}



