import { Component, OnInit } from '@angular/core';
import {DataService} from '../../service/data.service'
import {SocketService} from '../../service/socket.service'
@Component({
  selector: 'app-sensor',
  templateUrl: './sensor.component.html',
  styleUrls: ['./sensor.component.css']
})
export class SensorComponent implements OnInit {
  status_color:string;
  status_text:string;
  status_show:boolean;

  constructor(private dataservice:DataService, private io:SocketService) { 
    this.io.socket.on("DETECTING_SUCCESS", (data:any)=>{
      this.status_color = "#62fc62";
      this.status_text = "Successful"
      this.status_show = true
    })

  }

  ngOnInit() {
    
  }

  Update():void{
    this.status_color = "#9c9e9c";
    this.status_text = "Updating.."
    this.status_show = true
    var sensors = {
      'ec': this.dataservice.ec_detecting,
      'ph': this.dataservice.ph_detecting,
      'co2': this.dataservice.co2_detecting
    }
    this.io.socket.emit('UPDATE_DETECTING', sensors)
  }

  



}



