import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { SummaryComponent } from '../summary/summary.component';
import { ConfigComponent } from '../config/config.component'
import {SensorComponent} from '../config/sensor/sensor.component'
import {TimerComponent} from '../config/timer/timer.component'
import {ConnectionGuard} from '../disconnect/connection-guard'
import {DisconnectComponent} from '../disconnect/disconnect.component'
const routes: Routes= [
	{ path: '', redirectTo: '/summary', pathMatch: 'full' },
	{ path: 'summary',  component: SummaryComponent, canActivate: [ConnectionGuard] },
  { path: 'disconnected',  component: DisconnectComponent },
	{ path: 'config',  component: ConfigComponent , canActivate: [ConnectionGuard] , children:[
    {path: 'sensor', component:SensorComponent},
    {path: 'timer', component:TimerComponent}
  ]}
]

@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [
  	RouterModule
  ]
})
export class AppRoutingModule { }



